const apiKey = process.env.REACT_APP_API_KEY;
console.log(apiKey);

const navigatorFn = () => {
  if (navigator.geolocation) {
    function saveCoords(coordsObj) {
      localStorage.setItem("COORDS", JSON.stringify(coordsObj));
      //localstorageì˜ key, value ê°’ì€ ëª¨ë‘ string íƒ€ì…ìœ¼ë¡œ ì €ì¥ë˜ê¸°ë•Œë¬¸ì— ë³€í™˜ì‹œì¼œì¤€ë‹¤.
    }

    function handleSuccess(position) {
      const latitude = position.coords?.latitude;
      const longitude = position.coords?.longitude;
      const coordsObj = {
        // ê°ì²´ì˜ key,  value ê°’ì´ ë™ì¼í•  ë•Œì—ëŠ” í•œë²ˆë§Œ ì¨ì¤˜ë„ ëœë‹¤.
        latitude, // localStorageì— ê°ì²´ë¡œ valueì— ì €ì¥í•˜ê¸°ìœ„í•´ì„œ ê°ì²´ì— ë„£ì–´ì¤€ë‹¤.
        longitude,
      };
      saveCoords(coordsObj); // localStorageì— ìœ„ì¹˜ ì €ì¥
    }

    function handleError() {
      console.log("cant not access to location");
    }

    function askForCoords() {
      navigator.geolocation.getCurrentPosition(handleSuccess, handleError);
    }

    let loadedCoords = localStorage.getItem("COORDS");
    console.log(
      "loadCoords ë¡œì»¬ì €ì¥ì†Œì—ì„œ ì €ì¥ëœ ìœ„ë„ê²½ë„ ê°€ì ¸ì˜¤ê¸° : ",
      JSON.stringify(loadedCoords)
    );
    loadedCoords = JSON.parse(loadedCoords);

    if (loadedCoords === null) {
      // localStorageì— ì¢Œí‘œê°’ì´ ì €ì¥ë˜ì–´ìˆì§€ì•Šë‹¤ë©´
      askForCoords(); // ì¢Œí‘œê°’ì„ ë¬¼ì–´ë³¸ë‹¤
    }
    const xyresult = dfs_xy_conv(
      "toXY",
      Number(loadedCoords?.latitude),
      Number(loadedCoords?.longitude)
    );
    return xyresult;
  } //if
}; //navigatorFn

export const weatherapi = async ({ weatherDate, weatherTime }) => {
  // const {  } = params;

  var weatherapiresult = null;

  const xyresult = navigatorFn();
  console.log("weatherapi() ë‚´ ìœ„ê²½ë„ë¥¼ xyë³€ê²½í›„ : ", xyresult);
  console.log("weatherapi() ë‚´ weatherDate í™•ì¸ ->  ", weatherDate);
  console.log("weatherapi() ë‚´ weatherTime í™•ì¸ ->  : ", weatherTime);
  try {
    const result = await axios.get(URL, {
      params: {
        serviceKey: apiKey,
        numOfRows: 10,
        pageNo: 1,
        dataType: "JSON",
        base_date: weatherDate,
        base_time: weatherTime,
        nx: xyresult.x,
        ny: xyresult.y,
      },
    });
    weatherapiresult = result?.data?.response?.body?.items?.item;
    console.log("openAPI axios ê²°ê³¼ ", weatherapiresult);
    return weatherapiresult;
  } catch (e) {
    console.log("openAPI axios ì—ëŸ¬ë°œìƒ", e);
  }
}; //weatherapi
